				var tabArray = [];
				var array = [];
				let count = 0;
				for(let i in tabList){
					let data = {
						name: tabList[i].name,
						list: parseInt(i)
					}
					array.push(data);
					count++;
					if(count ==3){
						count =0;
						let objArray ={
							tabList:array
						}
						tabArray.push(objArray);
						array =[];
					} 
				}
				return tabArray;
